.content .select-project {
    margin-top: 3rem;
}

.content .game-conteiner {
    margin-top: 3rem;
}

.content h1 {
    margin-top: 3rem;
    text-align: center;
    display: block;
    margin-bottom: 0.5rem;
}

.nav-bar {
    width: 90%;
    max-width: 420px;
    min-width: 340px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.rotating {
    width: 70%;
    height: 70%;
}

