button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px var(--detail-color) solid;
    font-size: 0.8rem;
    margin: 0.25rem;
    height: 2rem;
    padding: 0.5rem;
}

button:hover {
    cursor:pointer;
    color: var(--hoover-color);
    border-color: var(--hoover-color);
}

