.projects {
    width: 90%;
    min-width: 380px;
    max-width: 600px;
    height: 100%;
    min-height: 320px;
    margin: 0 auto;
    border: 1px solid var(--detail-color);
    border-top: none;
}

.front {
    height: 100%;
}

.box {
    height: 100%;
}

.conteiner-box {
    height: 100%;
}

.select-project {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
}

.select-project button {
    margin: 0 0 0 0;
}

.description {
    text-align: center;
    width: 100%;
}

.projects .description h1 {
    margin: 1rem auto 1rem auto;
}

.description h2 {
    font-weight: lighter;
    margin: 0.5rem;
}

.description p {
    margin: 0.5rem;
}

.description a {
    display: block;
    margin: 0.5rem;
}

.cooperators {
    width: 80%;
    display: grid;
    grid-template-columns: auto repeat(3, 1fr);
    margin: 0 auto;
}

.desc p {
    display: block;
    width: 100%;;
}


